import { lazy, useContext } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import { AuthContext } from "../context";
// import { useAuthenticatedUser } from "../context/hooks";
// import { UserRoles } from "../utils/constants";
import { PrivatePaths } from "./path";

const paths = [
  {
    path: "",
    element: lazy(() => import("../modules/Home")),
  },
  {
    path: "webinar",
    element: lazy(() => import("../modules/Webinar")),
  },
  {
    path: "thank-you",
    element: lazy(() => import("../modules/Home/ThankYou")),
  },
  {
    path: "login",
    element: lazy(() => import("../modules/Admin/Login")),
  },

  {
    path: "*",
    element: lazy(() => import("../modules/NotFound")),
  },
];

function Auth() {
  const { user } = useContext(AuthContext);
  if (user) {
    return <Navigate to={`${PrivatePaths.DASHBOARD}`} replace />;
  }

  return (
    <Routes>
      {/* <Route path="" element={<Navigate to={PublicPaths.LOGIN} replace />} /> */}
      {paths.map(({ path, element: Element }) => (
        <Route key={path} path={path} element={<Element />} />
      ))}
    </Routes>
  );
}

export default Auth;
